import uniSteps from '@/components/uni-steps/uni-steps.vue'
import uniGrid from "@/components/uni-grid/uni-grid.vue"
import uniGridItem from "@/components/uni-grid-item/uni-grid-item.vue"
import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	data() {
		return {
			showexpen: false,
			showvip: false,
			content:'',
			type:'',
			list: [{
					url: '/static/indexImg/shoucang2.png',
					text: '免费题库'
				},
				{
					url: '/static/indexImg/shoucang2.png',
					text: '免费题库'
				},
				{
					url: '/static/c2.png',
					text: '免费题库'
				},
				{
					url: '/static/c3.png',
					text: '免费题库'
				},
				{
					url: '/static/indexImg/shoucang2.png',
					text: '免费题库'
				}]
		};
	},
	components: {
		uniSteps,
		uniGrid,
		uniGridItem,
		uniPopup
	},
	methods:{
		togglePopup(type) {
			switch (type) {
				case 'expen':
					this.content = '免费体验7天'
					this.$refs.expen.open()
					break		
				case 'vip':
					this.content = '开通会员'
					this.$refs.vip.open()
					break
			}
		},
	}
}
